import React, { useContext } from 'react';
import Todo from './Todo';
import TodoContext from './../../context/todoContext';
import { useTranslation } from 'react-i18next';

const Todos = () => {
    const context = useContext(TodoContext);
    const { todos, deleteTodo, completedTodo, changeTodo } = context;

    const { t, i18n } = useTranslation();

    const changeLangs = (lang) => {
        i18n.changeLanguage(lang);
    };

    return (
        <div>
            <div className='m-3 d-flex justify-content-center'>
                <div className='card w-25'>
                    <div className='card-body'>
                        <div className='cntr'>
                            <label
                                for='rdo-1'
                                className='btn-radio'
                                onClick={() => changeLangs('fa')}
                            >
                                <input
                                    type='radio'
                                    id='rdo-1'
                                    name='radio-grp'
                                />
                                <svg
                                    width='20px'
                                    height='20px'
                                    viewBox='0 0 20 20'
                                >
                                    <circle cx='10' cy='10' r='9' />
                                    <path
                                        d='M10,7 C8.34314575,7 7,8.34314575 7,10 C7,11.6568542 8.34314575,13 10,13 C11.6568542,13 13,11.6568542 13,10 C13,8.34314575 11.6568542,7 10,7 Z'
                                        className='inner'
                                    />
                                    <path
                                        d='M10,1 L10,1 L10,1 C14.9705627,1 19,5.02943725 19,10 L19,10 L19,10 C19,14.9705627 14.9705627,19 10,19 L10,19 L10,19 C5.02943725,19 1,14.9705627 1,10 L1,10 L1,10 C1,5.02943725 5.02943725,1 10,1 L10,1 Z'
                                        className='outer'
                                    />
                                </svg>
                                <span>Persian</span>
                            </label>
                            <label
                                for='rdo-2'
                                className='btn-radio'
                                onClick={() => changeLangs('en')}
                            >
                                <input
                                    type='radio'
                                    id='rdo-2'
                                    name='radio-grp'
                                />
                                <svg
                                    width='20px'
                                    height='20px'
                                    viewBox='0 0 20 20'
                                >
                                    <circle cx='10' cy='10' r='9' />
                                    <path
                                        d='M10,7 C8.34314575,7 7,8.34314575 7,10 C7,11.6568542 8.34314575,13 10,13 C11.6568542,13 13,11.6568542 13,10 C13,8.34314575 11.6568542,7 10,7 Z'
                                        className='inner'
                                    />
                                    <path
                                        d='M10,1 L10,1 L10,1 C14.9705627,1 19,5.02943725 19,10 L19,10 L19,10 C19,14.9705627 14.9705627,19 10,19 L10,19 L10,19 C5.02943725,19 1,14.9705627 1,10 L1,10 L1,10 C1,5.02943725 5.02943725,1 10,1 L10,1 Z'
                                        className='outer'
                                    />
                                </svg>
                                <span>English</span>
                            </label>
                        </div>
                    </div>
                </div>
            </div>

            <ul className='list-group list-group-flush p-xs'>
                {todos.length > 0 ? (
                    todos.map((todo) => (
                        <li key={todo.id} className='list-group-item'>
                            <Todo
                                text={todo.text}
                                isCompleted={todo.completed}
                                deleted={() => deleteTodo(todo.id)}
                                completed={() => completedTodo(todo.id)}
                                changed={(event) => changeTodo(event, todo.id)}
                            />
                        </li>
                    ))
                ) : (
                    <div className='alert alert-light mt-3 w-75 mx-auto'>
                        <p className='text-center'>{t('Add New Todo')}</p>
                    </div>
                )}
            </ul>
        </div>
    );
};

export default Todos;
